<template>
  <div class="setting-container">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <!--  -->
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item><a href="/"></a></el-breadcrumb-item>
        </el-breadcrumb>
      </div>

      <el-row>
        <!--  -->
        <el-col :span="15">
          <el-form
            ref="userForm"
            :model="user"
            label-width="80px"
            :rules="userRules"
          >
            <el-form-item label="">
              {{ user.id }}
            </el-form-item>
            <el-form-item label="">
              {{ user.mobile }}
            </el-form-item>
            <el-form-item label="" prop="name">
              <el-input v-model="user.name"></el-input>
            </el-form-item>

            <el-form-item label="" prop="intro">
              <el-input type="textarea" v-model="user.intro"></el-input>
            </el-form-item>
            <el-form-item label="" prop="email">
              <el-input v-model="user.email"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onSubmit" :loading="saveLoading"
                ></el-button
              >
            </el-form-item>
          </el-form>
        </el-col>
        <el-col :span="5" :offset="4" class="avatar">
          <!--  -->
          <label for="file" class="avatar-wrap">
            <el-avatar :size="150" fit="cover" :src="user.photo"></el-avatar>
            <el-tag class="avatar-title"></el-tag>
          </label>
          <input
            type="file"
            hidden
            id="file"
            ref="imgInput"
            @change="onUploadChange"
          />
        </el-col>
      </el-row>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="imgDialogVisible"
      append-to-body
      @opened="onOpen"
      @closed="onClose"
    >
      <!--  -->
      <div class="preview-wrap">
        <img :src="previewImage" alt="" class="preview-image" id="image" />
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="imgDialogVisible = false"> </el-button>
        <el-button
          type="primary"
          @click="onSuccessSumbit"
          :loading="sucessLoading"
          > </el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
import "cropperjs/dist/cropper.css";
import Cropper from "cropperjs";
import { getUserInfo, updateUserPhoto, updateUserInfo } from "@/api/user.js";
import globalBus from "@/utils/global-bus";
export default {
  name: "settingIndex",
  components: {},
  props: {},
  data() {
    return {
      user: {}, //
      imgDialogVisible: false, //
      previewImage: "", //
      cropper: null, //
      sucessLoading: false, //
      saveLoading: false, //
      //
      userRules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          { min: 1, max: 7, message: " 1  7 ", trigger: "blur" },
        ],
        intro: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        email: [
          { required: true, message: "", trigger: "blur" },
          {
            type: "email",
            message: "",
            trigger: ["blur", "change"],
          },
        ],
      },
    };
  },
  watch: {},
  computed: {},
  methods: {
    //
    onSubmit() {
      this.$refs["userForm"].validate(async (valid) => {
        if (!valid) {
          return;
        }
        this.saveLoading = true;
        const data = {
          name: this.user.name,
          intro: this.user.intro,
          email: this.user.email,
        };
        const { data: res } = await updateUserInfo(data);
        this.$message({
          message: "",
          type: "success",
        });
        //vuex
        // this.userInfo.name = res.data.name;
        //
        globalBus.$emit("updateName", res.data.name);
        this.saveLoading = false;
      });
    },
    //
    async loadUserInfo() {
      const { data: res } = await getUserInfo();
      this.user = res.data;
    },
    //
    onUploadChange() {
      //
      const file = this.$refs["imgInput"];
      const blob = window.URL.createObjectURL(file.files[0]);
      this.previewImage = blob;
      //
      this.imgDialogVisible = true;
      //
      this.$refs["imgInput"].value = "";
    },
    //dialog
    onOpen() {
      //
      const image = document.getElementById("image");
      // if (this.cropper) {
      //   //
      //   this.cropper.replace(this.previewImage);
      //   return;
      // }
      this.cropper = new Cropper(image, {
        viewMode: 1,
        dragMode: "move",
        aspectRatio: 1,
        cropBoxMovable: false,
        cropBoxResizable: false,
        background: false,
        movable: true,
      });
    },
    //dialog
    onClose() {
      //
      this.cropper.destroy();
    },
    //
    onSuccessSumbit() {
      //
      this.sucessLoading = true;
      this.cropper.getCroppedCanvas().toBlob(async (file) => {
        const fd = new FormData();
        fd.append("photo", file);
        //
        await updateUserPhoto(fd);
        //
        this.imgDialogVisible = false;
        this.$message({
          message: "",
          type: "success",
        });
        //
        this.user.photo = window.URL.createObjectURL(file);
        //
        globalBus.$emit("updateImg", this.previewImage);
        //
        this.sucessLoading = false;
      });
    },
  },
  created() {
    this.loadUserInfo();
  },
  mounted() {},
};
</script>
<style lang="less" scoped>
.setting-container {
  .avatar-wrap {
    position: relative;

    .avatar-title {
      position: absolute;
      bottom: -40px;
      left: 30px;
    }
  }
}
.preview-wrap {
  .preview-image {
    display: block;
    max-width: 100%;
    height: 200px;
  }
}
</style>
